.App {
  font-family: Arial, sans-serif;
  background-color: #121212;
  color: #ffffff;
  min-height: 100vh;
  padding: 20px;
}

.dashboard {
  display: flex;
  flex-direction: row;
  min-height: calc(100vh - 40px);
  align-items: stretch;
}

/* Left Container */
.left {
  flex: 1;
  position: relative; /* Enable absolute positioning */
  display: flex;
  align-items: center;
  justify-content: center;
  border-right: 1px solid rgba(255, 255, 255, 0.2);
}

/* Right Container */
.right {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

/* Bitcoin Price Tracker Styles */
.bitcoin-price-tracker {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.app-logo {
  position: absolute;
  top: 20px;
  left: 20px;
  width: 150px;
  height: auto;
}

h2 {
  color: #ff9800;
  margin-bottom: 10px;
}

.label,
select,
input {
  display: block;
  background-color: #1e1e1e;
  color: #ffffff;
  border: 1px solid #333333;
  padding: 5px;
}

p {
  font-size: 1.2em;
}

.bitcoin-price {
  font-size: 2.5em;
  margin: 10px 0;
}

.bitcoin-price-tracker select {
  margin-top: 10px;
  padding: 5px;
  background-color: #1e1e1e;
  color: #ffffff;
  border: 1px solid #333333;
}

/* Satoshi Equivalent Calculator Styles */
.satoshi-equivalent-calculator {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

/* Styles for the Change Amount button */
.change-amount-button {
  padding: 10px 20px;
  font-size: 1em;
  margin-top: 10px;
  background-color: #000000;
  color: rgba(255, 255, 255, 0.497);
  border: 1px solid rgba(255, 255, 255, 0.3);
  border-radius: 5px;
  cursor: pointer;
}

.change-amount-button:hover {
  background-color: #1a1a1a;
}

/* Styles for the Set button */
.set-amount-button {
  padding: 10px 20px;
  font-size: 1em;
  margin-top: 10px;
  background-color: #ff9800;
  color: #ffffff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.set-amount-button:hover {
  background-color: #e68900;
}

.input-container {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.input-container input {
  width: 100px;
  padding: 10px;
  font-size: 1em;
  border-radius: 5px;
  background-color: #1e1e1e;
  color: #ffffff;
  border: 1px solid #555555;
  margin-right: 5px;
}

.input-container input:focus {
  outline: none;
  border-color: #ff9800;
}

.input-container span {
  font-size: 1em;
  margin-right: 5px;
}

.input-container .set-amount-button {
  padding: 10px 15px;
  margin-left: 5px;
  margin-top: 0;
}

.satoshi-text {
  font-size: 1em;
  margin: 10px 0 5px 0;
}

.satoshi-amount {
  font-size: 2.5em;
  margin: 5px 0;
}

@media (max-width: 768px) {
  .dashboard {
    flex-direction: column;
  }

  .left {
    border-right: none;
    border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  }

  .app-logo {
    position: static;
    margin-top: 20px;
    margin-bottom: 20px;
  }
}
